h3,
h4 {
  margin-bottom: 1rem;
}

h4 {
  font-style: normal;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: var(--bg);
  color: var(--black);
  max-width: 100%;
}
/* UTILITIES */

.container {
  width: 100vw;
  max-width: 100%;
  padding: 2rem;
}

.profile {
  max-width: 100%;
  height: auto;
  max-height: 30vh;
  object-fit: contain;
}

.flexed {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 5rem;
}

.stack{
  margin-bottom: 0;
}

.split{
  margin-left: auto;
  margin-right: auto;
  height: 3px;
  width: 70%;
  background: radial-gradient(var(--black) 0, var(--bg) 75%);
  margin-bottom: 1rem;
}

.section2 {
  width: 75%;
}

.name {
  text-decoration: none;
  font-weight: 600;
}

.text {
  max-width: 50vw;
  display: inline;
  text-align: left;
}

.nameSubtext{
  margin-left: 50em;
}


/* Tablet and Mobile */
@media screen and (max-width: 1024px) {
  .profile {
    display: block;
    margin-right: auto;
    margin-left: auto;
    width: 50%;
  }
  .flexed {
    flex-direction: column;
    margin-bottom: 2rem;
  }
  .name h2 {
    font-size: 2rem;
  }  
  .text{
    text-align: center;
  }
}



